import cx from "classnames";
import React from "react";

import { Props as ContentProps } from "../types";
import { Cell } from "./util";

type Props = Readonly<{
  cell: Cell;
  onSelectBlock: ContentProps["onSelectBlock"];
  selectedBlock: ContentProps["selectedBlock"];
}>;

export const Header = ({ cell, onSelectBlock, selectedBlock }: Props) => {
  const selected = !!selectedBlock && selectedBlock === cell.id;
  return (
    <th
      className={cx({ selected })}
      colSpan={cell.colSpan}
      onClick={() => {
        if (!cell.id || !onSelectBlock) {
          return;
        }
        onSelectBlock(cell.id, cell.text);
      }}
      rowSpan={cell.rowSpan}
    >
      {cell.contents}
    </th>
  );
};
Header.displayName = "Header";
